* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.flex {
    display: flex;
}

.inline-flex {
    display: inline-flex;
}

.inline-table {
    display: inline-table;
}

.center {
    text-align: center;
    justify-content: center;
    align-items: center;
}

.between {
    justify-content: space-between;
}

.column {
    flex-flow: column;
}

.full-height {
    height: 100%;
}

.full-width {
    width: 100%;
}

.relative {
    position: relative;
}

.clickable {
    cursor: pointer;
}
h1, h2, h3, h4, h5, h6, pre {
    font-family: 'Courier New';
    font-weight: bold;
}

div, p, button, span, ul, li, .marker, textarea {
    font-family: 'Inconsolata';
}

body, .iframe-bg {
    background: #FCFFFF;
}

h1, h2, h3, h4, h5, h6, button, a {
    color: #1745AD;
}

p, pre, ul, li, textarea {
    color: #305EC6;
}

::selection {
    background: #C2F0FF;
}

::-moz-selection {
    background: #C9F7FF;
}

.active {
    color: #6391F9;
}

.logo {
    background: #96C4FF;
}

.title {
    color: #fefefe;
}

.title::selection {
    background: #305EC6;
}

.title::-moz-selection {
    background: #305EC6;
}

.brackets {
    color: #efefff;
}

article {
    background-color: #EFFFFF;
    border-top: 3px solid #305EC6;
    border-bottom: 3px solid #305EC6;
}

.clickable:hover:not(.header) {
    color: #6391F9;
}
h1 {
    font-size: 5vh;
}

button, pre {
    font-size: 4.5vh;
}

p, ul, li, textarea {
    font-size: 2.75vh;
}

ul {
    list-style: none;
}

textarea {
    resize: none;
}

header {
    margin-top: 7.5vh;
    margin-bottom: 2.5vh;
}

.nav {
    height: 4vh;
    justify-content: space-evenly;
}

.navpage {
    width: 25%;
}

.logo {
    height: calc((4vh * 5) + 30px);
    bottom: 6vh;
    border-radius: 100%;
    z-index: 1;
    padding: 20px;
}

.brackets {
    font-size: calc(4vh * 3.75);
    font-weight: normal;
    width: 40px;
}

.brackets, textarea {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.title-1, .marker pre {
    font-size: 2.5vh;
}

.title-2, h2 {
    font-size: 3.5vh;
}

.title-3 {
    font-size: 4.25vh;
}

button, textarea {
    background: none;
    border: none;
    outline: none;
}

article {
    padding: 50px 30px 35px 50px;
}

.img-content, .text-content {
    width: 50%;
}

.content, .text-content, textarea {
    line-height: 1.5;
}

.text-content {
    padding: 0 25px 0 75px;
}

.text-content * {
    margin-bottom: 10px;
}

.marker, .marker pre {
    margin-bottom: 0px;
}

.marker pre {
    align-self: flex-start;
}

.heading, .underline {
    text-decoration: underline;
}

.heading, .footnote {
    font-weight: bold;
}

.footnote {
    font-style: italic;
}

.project {
    height: -webkit-max-content;
    height: max-content;
}

.project * {
    margin-bottom: 0;
}

.project p {
    display: inline-block;
    margin-bottom: 5px;
}

.float-r {
    margin-right: 15px;
}

.source {
    height: 30vh;
}

.source * {
    border: 1px solid black;
}

.iframe-bg, .contact {
    height: 1000px;
    width: 640px;
}

.iframe-bg {
    width: 576px;
}

.contact iframe {
    height: inherit;
    width: inherit;
    right: 32px;
    bottom: 1030px;
    margin-bottom: -1000px;
    border: 0px solid black;
}

footer {
    padding: 30px 50px;
}

td {
    width: 12.5vh;
    margin-right: 10vh;
    white-space: nowrap;
    text-align: left;
}

.td-long {
    width: 16.5vh;
}

.link-row * {
    font-size: 2vh;
}

.socials img {
    height: 8vh;
    margin: 0 10px;
}

.space {
    margin-bottom: 20px;
}

a {
    text-decoration: none;
}
@media (orientation: portrait) {
    h1 {
        font-size: 7vw;
    }

    a, p {
        font-size: 5vw;
    }

    article {
        padding: 30px;
    }
}

@media (orientation: portrait) and (min-width: 500px) {
    h1 {
        font-size: 5.75vw;
    }

    header {
        height: 12%;
    }

    article {
        min-height: 88%;
    }

    .title {
        height: 45%
    }

    .nav {
        height: 50%;
    }
}

@media (orientation: portrait) and (min-width: 600px) {
    header {
        height: 20%;
    }

    article {
        min-height: 80%;
    }

    .title {
        height: 60%
    }

    .nav {
        height: 35%;
    }
}

@media (orientation: portrait) and (min-width: 850px) {
    header {
        height: 11%;
    }

    .title {
        height: 60%;
        margin: 20px;
    }

    article {
        min-height: calc(89% - 20px);
    }
}

@media (orientation: landscape) and (max-height: 700px) {
    header {
        height: 22%;
    }

    article {
        min-height: 78%;
    }
}

@media (orientation: landscape) and (max-height: 600px) {
    h1 {
        font-size: 5.5vh;
    }

    a, p {
        font-size: 3.5vh;
    }
}

@media (orientation: landscape) and (max-height: 500px) {
    a, p {
        font-size: 4vh;
    }
}

@media (orientation: landscape) and (max-height: 400px) {
    header {
        height: 24%;
    }

    article {
        min-height: 76%;
    }

    h1 {
        font-size: 6vh;
    }

    a, p {
        font-size: 4.5vh;
    }
}
